"use strict";

import RookError from "../processor/RookError";
import {RookRuleMaxExecutionTimeReached, RookRuleRateLimited} from "../exceptions";

import {logger} from "../logger";
import UserWarnings from "../UserWarnings";

import ContainerNamespace from "../processor/namespaces/ContainerNamespace"
import JSUtilsNamespace from "../processor/namespaces/JSUtilsNamespace"
import RateLimiter from "./RateLimiter";
const uuid4 = require("uuid/v4");

export default class Aug {
    constructor(augId, location, action, condition, output, maxAugTime, limits) {
        this.augId = augId;
        this.location = location;
        this.action = action;
        this.output = output;
        this.status = null;
        this.maxAugTime = maxAugTime;
        this.enabled = true;
        this.condition = condition;
        this._warningCache = new Map();
        this._logCache = new Map();

        if (limits !== undefined && limits.length > 0) {
            this.rateLimiter = new RateLimiter(limits[0], limits[1]);
        } else {
            this.rateLimiter = new RateLimiter();
        }
    }

    addAug(triggerServices) {
        try {
            this.location.addAug(triggerServices, this);
        } catch (e) {
            const message = "Exception when adding aug";
            logger.exception(message, e);
            this.setError(new RookError(e, message));
        }
    }

    execute(frame, stack) {
        if (!this.enabled) {
            return;
        }

        try {
            let startTime = Date.now();

            let namespace = new ContainerNamespace({
                'frame': frame,
                'stack': stack,
                'store': new ContainerNamespace({}),
                'temp': new ContainerNamespace({}),
                'utils': new JSUtilsNamespace({})
            });

            if ((this.condition === undefined) || this.condition.evaluate(namespace)) {
                let rateLimitKey = this.rateLimiter.allow(startTime);

                if (rateLimitKey === null) {
                    this.sendWarning(new RookError(new RookRuleRateLimited()));
                    return;
                }

                const msgId = uuid4().replace(/\-/g, "");
                logger.info("Executing aug-\t%s (msg ID %s)", this.augId, msgId)
                this.action.execute(this.augId, msgId, namespace, this.output, new UserWarnings(this));

                let duration = Date.now() - startTime;
                this.rateLimiter.record(rateLimitKey, duration);

                if (this.maxAugTime > 0 && duration > this.maxAugTime) {
                    this.enabled = false;
                    throw new RookRuleMaxExecutionTimeReached();
                }
            }
        } catch (e) {
            const message = "Exception while processing Aug";
            let rookError = new RookError(e, message);

            if (!this.shouldSilenceLog(rookError, this._logCache)) {
                logger.exception(message, e);
            }

            this.setError(rookError);
        }
    }

    setActive() {
        this.sendRuleStatus("Active");
    }

    setPending() {
        this.sendRuleStatus("Pending");
    }

    setRemoved() {
        this.sendRuleStatus("Deleted");
    }

    setError(error) {
        this.sendRuleStatus("Error", error);
    }

    setUnknown(error) {
        this.sendRuleStatus("Unknown", error);
    }

    sendWarning(error) {
        if (this.shouldSilenceLog(error, this._warningCache)) {
            return;
        }

        logger.warn(error.message);

        this.output.sendWarning(this.augId, error);
    }

    sendRuleStatus(status, error=null) {
        if (this.status === status) {
            return;
        }

        logger.info("Updating rule status for %s to %s", this.augId, status);

        this.status = status;
        this.output.sendRuleStatus(this.augId, status, error);
    }

    shouldSilenceLog(error, logCache) {
        if (logCache.has(error.message) || logCache.size >= 10) {
            return true;
        }

        logCache.set(error.message, true);

        return false;
    }
}
